
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.clickable {
  cursor: pointer;
}
/* banner */
.hero {
  height: auto; /* grows according to text - won't need updating if I move the instructions*/
  background-image: url(/static/img/ffb-banner.b908899.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
}
.container {
  /* NOTE: checkout grid template areas - https://css-tricks.com/snippets/css/complete-guide-grid/ */
  height: 1200px;
  margin-left: 1em;
  margin-right: 1em;
  padding: 0;
  display: grid;
  grid-gap: 1em;
  grid-template-columns: 1fr 3fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 30% 30%;
}
.rankings {
  grid-column-start: 2;
  grid-row-start: 1;
}
.rbs {
  grid-column-start: 3;
  grid-row-start: 1;
}
.qbs {
  grid-column-start: 3;
  grid-row-start: 2;
  -ms-flex-item-align: start;
      align-self: start;
}
.wrs {
  /* grid-column-start: right-pos-start; */
  /* grid-column-end: right-pos-end; */
  /* grid-row-start: top-tbl-start;
  grid-row-end: top-tbl-end; */
  grid-column-start: 4;
  grid-row-start: 1;
}
.tes {
  /* grid-column-start: right-pos-start; */
  /* grid-column-end: right-pos-end; */
  /* grid-row-start: btm-tbl-start;
  grid-row-end: btm-tbl-end; */
  grid-column-start: 4;
  grid-row-start: 2;
  -ms-flex-item-align: start;
      align-self: start;
}
.drafted {
  /* grid-column-start: drafted-start;
  grid-column-end: drafted-end;
  grid-row-start: top-tbl-start; */
  grid-column-start: 5;
  grid-row-start: 1;
}
.orange {
  border: none;
  padding: 0 0 0 5px;
  color: orange;
}
/* Keeping the table header from scrolling with the body breaks the column sizing. Explicitly set them. */
/* Scroll bar also borks things. Width is fudged a bit to keep things close. Proably use Datatables in the future. */
.rankingsTable thead tr {
  width: 99%;
  display: block;
  border: 1px solid black;
}
.rankingsTable tbody {
  width: 101%;
  display: block;
  height: 1000px;
  overflow:auto;
}
.rankingsTable thead tr th {
  width: 7%;
}
.rankingsTable thead tr th:nth-child(2) {
  width: 20%;
}
.rankingsTable tbody tr td {
  width: 7%;
}
.rankingsTable tbody tr td:nth-child(2) {
  width: 20%;
}
.posTable {
  display:block;
  overflow:auto;
  height:300px;
  /* width: auto; */
}
.posTable tbody tr td:first-child {
  width: 80%;
}
.search {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.search results{
  min-height: 100px;
}
table, td {
  border-collapse: collapse;
  background-color: #ffffff; /* keep this in case the page bg color is changed */
  border: 1px solid black;
}
td {
  padding: 1px 2px 1px 2px;
}
tr:hover {
  background-color: #f5f5f5;
}

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.upload-button {
  position: relative;
  overflow: hidden;
  display: inline-block;
  color: orange;

  border: 2px solid black;
  border-radius: 5px;
  padding: 8px 12px;
  cursor: pointer;
}
.upload-button:hover {
  background-color: #f5f5f5;
}
.upload-button input {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  opacity: 0;
}
